}

.card-button {
    color: #FFF;
    background: #626ED4;
    border: none;
    border-radius: 5px;
    padding: .5rem 1.25rem;
    font-size: 14px;
    letter-spacing: 1px;
    margin: 10px 0 10px 0px;
    line-height: 1.5;
