    transition: 0.25s ease-out;
}

.card-button:hover {
    background: #535eb4;
}

.danger {
    background: #ec4561;
}

.good {
    background: #02a499;
}

.good:hover {
    background: #028b82;
}

.card-title-right {
    color: #FFF;
