.message {
    background: #f6f6f6;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-radius: 0px 4px 4px;
    padding: 8px 10px;
    width: -webkit-max-content;
    width: max-content;
    margin-bottom: 10px;
    margin-left: 10px;
    position: relative;
    max-width: 50%;
    overflow-wrap: break-word;
}

.message::after {
    background: #f6f6f6;
    width: 10px;
    height: 10px;
    left: -9px;
    top: 0;
    content: " ";
    position: absolute;
    display: block;
    -webkit-clip-path: polygon(0 0, 100% 100%, 100% 0);
            clip-path: polygon(0 0, 100% 100%, 100% 0);
}

