    margin-bottom: 10px;
    margin-left: auto;
    margin-right: 15px;
    text-align: end;
    position: relative;
    max-width: 50%;
    overflow-wrap: break-word;
}

.message-self::after {
    background: #363e54;
    width: 10px;
    height: 10px;
